.section-about-program {
    padding: 5rem;
    padding-bottom: 8rem;
    background-color: #204987;
    color: #fff;
    @media only screen and (max-width: 56.25em) {
        padding: 2rem 0;
       }
}

.description {
    font-size: 2rem;
    margin-bottom: 2rem;
    @media only screen and (max-width: 56.25em) {
        font-size: 1.5rem;
        padding-bottom: 4rem;
        }
}

.about-text {
    font-size: 3rem;
    margin: 2rem 0;
    
    span {
        // text-shadow: 0 1rem 1rem rgba(0,0,0,0.15);
        color: #fff;
        font-weight:1000;
    }

    @media only screen and (max-width: 56.25em) {
       font-size: 2rem;
       margin: 4rem 0;
       }
}
